import React, { useState, useRef } from 'react'
import SimpleReactValidator from 'simple-react-validator';
import UserAction from '../redux/actions/TokenAction'
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import iziToast from 'izitoast'
import 'izitoast/dist/css/iziToast.css';
import Axios from 'axios';

const Login = (props) => {
    const [state, setState] = useState({
        username: '',
        password: '',
        isLoading: false
    });
	
    let history = useHistory();

    //validator
    const [, forceUpdate] = useState() //this is a dummy state, when form submitted, change the state so that message is rendered
    const validator = useRef(new SimpleReactValidator({
        autoForceUpdate: { forceUpdate: forceUpdate },
        className: 'small text-danger mdi mdi-alert pt-1 pl-1'
    }));

    const onChangeHandle = (e) => {
        const { name, value } = e.target;
        setState({
            ...state,
            [name]: value
        });
    }

    const onSubmitHandle = async (e) => {
        e.preventDefault();
        if (validator.current.allValid()) {
            setState({
                ...state,
                isLoading: true
            });

            try {
                const response = await Axios.post('/api/login', state);
                
                iziToast.show({
                    title: '',
                    message: "Login Successful",
                    messageSize: 12,
                    position: 'topRight',
                    theme: 'dark',
                    pauseOnHover: true,
                    color: '#565c70',
                    messageColor:'#00ffb8',
                    icon: 'mdi mdi-check'
                });
                //backend request
                var token = response.data.token;
                
                localStorage.setItem('token', token);
                props.setTokenProp(token);
                history.push('/');
            } catch (error) {
                
                iziToast.show({
                    title: '',
                    message: error.response.data.message,
                    messageSize: 12,
                    position: 'topRight',
                    theme: 'dark',
                    pauseOnHover: true,
                    color: '#565c70',
                    messageColor:'#ffafb4',
                    icon: 'mdi mdi-alert-circle-outline'
                });
            }


            
        } else {
            validator.current.showMessages();
            forceUpdate(1);
        }
    }

    return (
        <React.Fragment>
            <div className="container-scroller">
                <div className="container-fluid page-body-wrapper full-page-wrapper">
                    <div className="content-wrapper d-flex align-items-center auth">
                        <div className="row flex-grow">
                            <div className="col-lg-4 mx-auto">
                                <div className="auth-form-light text-left p-5 animated fadeIn">
                                    <div className="brand-logo">
                                        <h1 className="text-center" style={{ color: '#da8cff' }}>Visitor Tracker</h1>
                                    </div>
                                    <form className="pt-3" onSubmit={(e) => {
                                        onSubmitHandle(e)
                                    }}>
                                        <div className="form-group">
                                            <input type="text" className="form-control form-control-lg" name="username" id="username" placeholder="Username" value={state.username} onChange={onChangeHandle} />
                                            {validator.current.message('username', state.username, 'required')}
                                        </div>
                                        <div className="form-group">
                                            <input type="password" className="form-control form-control-lg" name="password" id="password" placeholder="Password" value={state.password} onChange={onChangeHandle} />
                                            {validator.current.message('password', state.password, 'required')}
                                        </div>
                                        <div className="mt-3">
                                            <button type="submit" className="btn btn-block btn-gradient-primary btn-lg font-weight-medium auth-form-btn" >
                                                LOGIN
                                            </button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
};

//redux state can be accessed as props in this component
const mapStateToProps = (state) => {
    return {
        tokenProp: state.TokenReducer
    }
}

/**
 * redux state can be change by calling 'props.setUserProp('demo user');'
 */
const mapDispatchToProps = (dispatch) => {
    return {
        setTokenProp: (token) => dispatch(UserAction(token))
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);